<template>
    <ui-accordion css="styled fluid" v-if="item.owner_id===user_id||item.state==''" >
      <ui-title><ui-icon css="dropdown"></ui-icon>
      {{item.title}}[{{item.state}}]
      <label v-if="!!user_id">----{{item.owner}}[{{item.ownerMobile}}] <label>
      </ui-title>
      <ui-content>{{{item.description}}}
       <br>

      <ui-button v-if="user_id&&item.state==''&&item.owner_id!=user_id" @click="requestBook(item,mobile)"></ui-button>
      <br>
      <div v-if="item.owner_id===user_id&&''!=item.requesterMobile">
        <label>{{item.requesterMobile}}--{{item.recDate | timeToNow}}</label>
        <br>
        <label></label>
        <br>
        <select v-model="state">
          <option selected></option>
          <option></option>
        </select>
       
        <ui-button css="basic" @click='confirmBook(item,state)'>
             
        </ui-button>
       
      </div>
      </ui-content>
    </ui-accordion>
</template>

<script>
  import { requestBook,confirmBook } from '../vuex/actions'

export default {
  props:["item"],
  data:()=>{
    return {state:''}
  },
  vuex: {
    getters: {
      user_id :({ user }) => user.auth_id,
      mobile :({ user }) => user.mobile
    },
    actions: {
      requestBook,confirmBook
    }
  }
}
</script>


